@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
@font-face {
  font-family: "Katex";
  src: url(/svd-visualisation/static/media/katex-regular.1f59b6fa.ttf) format("truetype");
}

@font-face {
  font-family: "Katex";
  src: url(/svd-visualisation/static/media/katex-bold.fa009f3b.ttf) format("truetype");
  font-weight: bold;
}

@font-face {
  font-family: "Katex";
  src: url(/svd-visualisation/static/media/katex-italic.8e740c89.ttf) format("truetype");
  font-style: italic;
}

@font-face {
  font-family: "Katex";
  src: url(/svd-visualisation/static/media/katex-bolditalic.cd9f7de5.ttf) format("truetype");
  font-weight: bold;
  font-style: italic;
}

.katex-html {
  display: none;
}

body {
  color: #fff;
  background-color: #000;
  font-family: "Katex";
  overflow: hidden;
}

a {
  text-decoration: none;
  color: white;
  font-style: italic;
  transition: 0.3s linear;
}

a:hover {
  opacity: 0.4;
}

.open2 {
  grid-column: 1;
  grid-row: 1 / span 2;
}

.open3 {
  grid-column: 1;
  grid-row: 1 / span 3;
}

.close2 {
  grid-column: 5;
  grid-row: 1 / span 2;
}

.close3 {
  grid-column: 7;
  grid-row: 1 / span 3;
}

.a, .b, .e {
  grid-row: 1;
}

.c, .d, .f {
  grid-row: 2;
}

.g, .h, .i {
  grid-row: 3;
}

.a, .c, .g {
  grid-column: 2;
}

.b, .d, .h {
  grid-column: 4;
}

.e, .f, .i {
  grid-column: 6;
}

.matrix {
  font-size: 14px;
  display: flex;
  align-items: center;
  transition: 0.2s;
}

.matrix:hover {
  opacity: 0.4;
  cursor: pointer;
}
